import { useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
import HeadHome from "../../Components/Header";
import Footer from "../../Components/Footer";
import Header from "./Header";
import Main from "./Main";
import Lists from "../../Components/Lists";
import { Api } from "../../Api";

export default function Index() {
  const { pathname } = useLocation();
  //------- Get and Set data form api
  const [contentapi, setcontentapi] = useState({});

  const valueParameter = pathname.substring(10);

  async function uri(urlparameter) {
    const response = await Api(urlparameter.substring(10));
    setcontentapi(response);
    // let index = "https://core.gapfilm.ir/api/v3.1/";

    // let homepage;
    // if (pathname === "/contents/Movies") {
    //   index = "https://core.gapfilm.ir/api/v3.3/";
    //   homepage =
    //     index +
    //     `GetFirstPageByPlatform?PageSize=8&PageIndex=0&ContentRows=11&PlatformId=1&ParentEntityId=6&ParentType=2`;
    // }else if (pathname === "/contents/Kids") {
    //   index = "https://core.gapfilm.ir/api/v3.3/";
    //    homepage =
    //      index +
    //      `GetFirstPageByPlatform?PageIndex=0&PageSize=8&ContentRows=11&PlatformId=9&PageType=1&PlatformType=1&ParentType=2`;
    // } else {
    //   homepage = index + `GetContent?Id=${id}&PageIndex=0`;
    // }

    // const response = await fetch(homepage);

    // if (!["/contents/Serial", "/contents/Kids", "/contents/Movies"].includes(pathname)) {
    //   const { Result, Status } = await response.json();
    //   setcontentapi(Result);
    // } else {
    //   const { Result: { Sections } } = await response.json();
    //   setcontentapi(Sections);
    // }
  }

  useEffect(() => {

    uri(pathname);
    //  saveNewPerson();
  }, [pathname]);

  if (('Result' in contentapi)) {
    const { Result } = contentapi;
    return (
      <>
        <HeadHome />

        {('AttachmentList' in Result) ? (
          <Header headerValue={Result} />
        ) : null}

        {["Kids", "Movies", "Crime", "Actions", "Adventure", "History", "Horror", "Mystery"].includes(valueParameter) && Array.isArray(Result.Sections) ? (
          <div className="pt-20">
            {Result.Sections.map(({ Title, ContentSummaryRows, Image }, index) => {
              if (!Title.includes("اسلایدر")) {
                return (
                  <Lists
                    key={index}
                    title={Title}
                    listValue={ContentSummaryRows}
                    background={Image}
                  />
                );
              }
            })}
          </div>
        ) : ('AttachmentList' in Result) && (
          <Main mainValue={Result} />
        )}

        <Footer />
      </>
    );
  }
}
